import back from '@/components/back'
import Editor from 'wangeditor'

export default {
    components:{
        back
    },
    data(){
        return {
            info: {
                introduce: '',
                name: '',
                minAge: 0,
                maxAge: 99,
                number: '',
                sex: '0',
                DiseaseName: [],
                needPost: 0,
                ServiceTempletId: '',
                ServiceTempletName: '',
            },
            search: '',
            DiseaseList: null,
            service: this.$store.state.service,
            editor: null
        }
    },
    created(){
        if(this.service){
            this.info.introduce = this.service.Description
            this.info.name = this.service.ServicePackName || this.service.PackTempletName
            this.info.minAge = this.service.AgeStart
            this.info.maxAge = this.service.AgeEnd
            this.info.number = this.service.MaxBuyNum
            this.info.sex = this.service.Sex
            this.info.needPost = this.service.IsDelivery
            this.info.ServiceTempletId = this.service.ServiceTempletId
            this.info.ServiceTempletName = this.service.ServiceTempletName
            if(!this.service.DiseaseNames || !this.service.DiseaseNames.length){
                this.service.DiseaseNames = []
            }
            else{
                this.info.DiseaseName = this.service.DiseaseNames
            }
        }
        if(this.$store.state.serviceBase){
            this.info = this.$store.state.serviceBase
            this.info.DiseaseName = this.$store.state.Disease
        }
    },
    mounted(){
        this.editor = new Editor('#editorElem')
        this.editor.customConfig.menus = []
        this.editor.customConfig.onchange = (html) => {
            this.info.introduce = html
        }
        this.editor.customConfig.onblur = function (html) {
            this.info.introduce = html
        }
        this.editor.create()
        const txt = this.$store.state.serviceBase ? this.$store.state.serviceBase.introduce : this.info.introduce
        this.$nextTick(() => {
            this.editor.txt.html(txt)
        })
        
    },
    beforeDestroy(){
        this.info.introduce = this.editor.txt.html()
        this.$store.dispatch('pushserviceBase', this.info)
        this.$store.dispatch('pushDisease', this.info.DiseaseName)
    },
    methods: {
        pushTo(i){
            this.info.DiseaseName.push(i.DiseaseName)
        },
        pop(idx){
            this.info.DiseaseName.splice(idx,1)
        },
        toNext(){
            this.info.introduce = this.editor.txt.html()
            if(!this.info.name){
                return this.$toast('请输入服务包名称')
            }
            if(!this.info.introduce){
                return this.$toast('请输入服务介绍')
            }
            if(!this.info.minAge || !this.info.maxAge){
                if(!(this.info.minAge == 0 || this.info.maxAge == 0)){
                    return this.$toast('请输入年龄')
                }
            }
            if(!this.info.sex){
                if(this.info.sex != 0){
                    return this.$toast('请选择性别')
                }
            }
            if(!this.info.number){
                return this.$toast('请输入销售数量')
            }
            this.$store.dispatch('pushserviceBase', this.info)
            if(this.$route.query.fromTemp){
                this.$router.push(`/create-sevice?fromTemp=${this.$route.query.fromTemp}`)
            }
            else{
                this.$router.push(`/create-sevice`)
            }
        }
    }
}



